import { matchRoutes, useLocation } from "react-router-dom"
import {
  BRIEF_ROUTE,
  CHAT_ROUTE,
  FILES_ROUTE,
  HOME_ROUTE,
  SEARCHES_ROUTE,
  SEARCH_ID_ROUTE,
  SEARCH_ROUTE,
  TERMS_AND_POLICIES_ROUTE,
  WATCHLISTS_ROUTE,
  WATCHLIST_EDIT,
  WATCHLIST_EDIT_NEW,
  WATCHLIST_ID_ROUTE,
  WATCHLIST_NEW,
  WORKFLOW_ID_ROUTE,
} from "./routeConstants"

const routes = [
  { path: HOME_ROUTE },
  { path: SEARCHES_ROUTE },
  { path: WATCHLISTS_ROUTE },
  { path: FILES_ROUTE },
  { path: WATCHLIST_ID_ROUTE },
  { path: WATCHLIST_NEW },
  { path: WATCHLIST_EDIT_NEW },
  { path: WATCHLIST_EDIT },
  { path: SEARCH_ID_ROUTE },
  { path: SEARCH_ROUTE },
  { path: TERMS_AND_POLICIES_ROUTE },
  { path: CHAT_ROUTE },
  { path: BRIEF_ROUTE },
  { path: WORKFLOW_ID_ROUTE },
]

const useRouteTracker = () => {
  const location = useLocation()

  const [result] = matchRoutes(routes, location) || []
  return result?.route?.path ?? HOME_ROUTE
}
export { useRouteTracker }
